import { Box, Button, Divider, ListItemButton } from '@mui/material';

// third-party
import { size, includes } from 'lodash';

// project
import CommonCard from 'components/cards/CommonCard';

const sumMails = (mails, condition) => {
    // return sum(mails.filter((m)=> includes(m.)))
    let filteredMails = 0;
    switch (condition) {
        case 'Inbox':
            filteredMails = mails.filter((mail) => includes(mail.status, condition))
            break;
        case 'Drafts':
            filteredMails = mails.filter((mail) => includes(mail.status, condition))
            break;
        case 'Trash':
            filteredMails = mails.filter((mail) => includes(mail.status, condition))
            break;
        case 'Sent':
            filteredMails = mails.filter((mail) => includes(mail.status, condition))
            break;
        case 'Imponant':
            filteredMails = mails.filter((mail) => includes(mail.label, condition))
            break;
        case 'Company':
            filteredMails = mails.filter((mail) => includes(mail.label, condition))
            break;
        case 'Personal':
            filteredMails = mails.filter((mail) => includes(mail.label, condition))
            break;
        case 'Family':
            filteredMails = mails.filter((mail) => includes(mail.label, condition))
            break;
        default: //stared
            filteredMails = mails.filter((mail) => mail.starred === true)
            break;
    }
    return size(filteredMails)
}
