import React from 'react'
import PropTypes from 'prop-types'
import { List, ListItem, ListItemText, Popover } from '@mui/material'

const CustomizedPopover = (props) => {

    const { items, open, anchorEl, handlePopoverClose } = props;
    return (
        <Popover
            id="mouse-over-popover"
            sx={{
            pointerEvents: 'none',
            }}
            open={open}
            anchorEl={anchorEl}
            anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
            }}
            transformOrigin={{
            vertical: 'top',
            horizontal: 'left',
            }}
            onClose={handlePopoverClose}
            disableRestoreFocus
        >
            <List
                sx={{
                    width: '100%',
                    maxWidth: 360,
                    bgcolor: 'background.paper',
                    position: 'relative',
                    overflow: 'auto',
                    maxHeight: 300,
                    '& ul': { padding: 0 },
                }}
                subheader={<li />}
            >
                {items?.map((item) => (
                    <ListItem key={`item-${item}`}>
                        <ListItemText primary={item} />
                    </ListItem>
                ))}

            </List>
        </Popover>
    )
}

CustomizedPopover.propTypes = {
    children: PropTypes.node

}
export default CustomizedPopover;