import React from 'react'
import PropTypes from 'prop-types';
// @mui
import { styled, alpha } from '@mui/material/styles';
import { Toolbar, Tooltip, IconButton, Typography, OutlinedInput, InputAdornment, Stack, Divider } from '@mui/material';
// component
import Iconify from 'components/iconify';
import { Alarm, Archive, Delete, LabelImportant, ManageSearch, MoveDown, Star } from '@mui/icons-material';
import CustomizedPopover from 'components/components/CustomizedPopover'
// ----------------------------------------------------------------------

const StyledRoot = styled(Toolbar)(({ theme }) => ({
  height: 96,
  display: 'flex',
  justifyContent: 'space-between',
  padding: theme.spacing(0, 1, 0, 3),
}));

const StyledSearch = styled(OutlinedInput)(({ theme }) => ({
  width: 240,
  transition: theme.transitions.create(['box-shadow', 'width'], {
    easing: theme.transitions.easing.easeInOut,
    duration: theme.transitions.duration.shorter,
  }),
  '&.Mui-focused': {
    width: 320,
    boxShadow: theme.shadows[6],
  },
  '& fieldset': {
    borderWidth: `1px !important`,
    borderColor: `${alpha(theme.palette.grey[500], 0.32)} !important`,
  },
}));

// ----------------------------------------------------------------------

MailListToolbar.propTypes = {
  numSelected: PropTypes.number,
  filterName: PropTypes.string,
  onFilterName: PropTypes.func,
};

export default function MailListToolbar({ numSelected, filterName, onFilterName }) {

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handlePopoverOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handlePopoverClose = () => {
    setAnchorEl(null);
  };

  const popoverOpen = Boolean(anchorEl);

  return (
    <StyledRoot
      sx={{
        ...(numSelected > 0 && {
          color: 'text.default',
          bgcolor: 'background.default',
        }),
      }}
    >


      {numSelected > 0 ? (
        <Stack direction={'row'} flexDirection={'flex-start'} alignItems={'center'} spacing={2}>
          <Typography component="div" variant="subtitle1" sx={{ flexGrow: 1 }}>
            {numSelected} selected
          </Typography>
          <Tooltip title="delete">
            <IconButton  >
              <Delete />
            </IconButton>
          </Tooltip>
          <Tooltip title="star">
            <IconButton  >
              <Star />
            </IconButton>
          </Tooltip>
          <Tooltip title="archived file">
            <IconButton >
              <Archive />
            </IconButton>
          </Tooltip>
          <Divider orientation="vertical" light variant="inset" flexItem />
          <Tooltip title="delay">
            <IconButton   >
              <Alarm />
            </IconButton>
          </Tooltip>
          <Tooltip title="move to">
            <IconButton  >
              <MoveDown />
            </IconButton>
          </Tooltip>
          <Tooltip title="Important">
            <IconButton onClick={handlePopoverOpen} >
              <LabelImportant />
            </IconButton>
          </Tooltip>
          <CustomizedPopover items={['Imponant', 'Company', 'Family', 'Personal']} open={popoverOpen} anchorEl={anchorEl} handlePopoverClose={handlePopoverClose} />
        </Stack>
      ) : (
        <>
          <StyledSearch
            value={filterName}
            onChange={onFilterName}
            placeholder="Search user..."
            startAdornment={
              <InputAdornment position="start">
                {/* <Iconify icon="eva:search-fill" sx={{ color: 'text.disabled', width: 20, height: 20 }} /> */}
                <ManageSearch sx={{ color: 'text.disabled', width: 28, height: 28 }} />
              </InputAdornment>
            }
          />
          <Tooltip title="Filter list">
            <IconButton>
              <Iconify icon="ic:round-filter-list" />
            </IconButton>
          </Tooltip>

        </>
      )
      }


    </StyledRoot>
  );
}
