import { faker } from '@faker-js/faker';
import { sample } from 'lodash';

export const MAIL_LABELS =['Imponant','Company','Personal','Family']

export const STATUS = ['Inbox', 'Sent','Drafts','Trash']

export const MAILS = [...Array(20)].map((_, index) => ({
 
  id: faker.datatype.uuid(),
  name:  faker.name.fullName(),
  avatarUrl: require(`assets/images/users/user${index+1}.png`),
  from:  faker.internet.email(),
  subject: faker.lorem.sentences(),
  to:  faker.internet.email(),
  content: faker.lorem.sentences(),
  starred: faker.datatype.boolean(),
  label: sample(MAIL_LABELS),
  status: sample(STATUS),
  createdAt: faker.date.past(),
}));

