import React, { useState } from 'react'
// @mui
import { Box } from '@mui/material'

// 
import { includes } from 'lodash'
// project
import MailSideBar from 'components/mail/MailSideBar'
import MailContent from './MailContent'
import PageContainer from 'layouts/PageContainer'
import PageHeading from 'layouts/PageHeading'

// mock
import { MAILS } from '__mocks__/mails';

let filteredMails = MAILS.filter((mail) => mail.status === 'Inbox');

export default function Mail() {
    const [mails, setMails] = useState(filteredMails)

    const handleMailStatus = (condition) => {
        switch (condition) {
            case 'Inbox':
                filteredMails = MAILS.filter((mail) => includes(mail.status, condition))
                break;
            case 'Sent':
                filteredMails = MAILS.filter((mail) => includes(mail.status, condition))
                break;
            case 'Drafts':
                filteredMails = MAILS.filter((mail) => includes(mail.status, condition))
                break;
            case 'Trash':
                filteredMails = MAILS.filter((mail) => includes(mail.status, condition))
                break;
            case 'Imponant':
                filteredMails = MAILS.filter((mail) => includes(mail.label, condition))
                break;
            case 'Company':
                filteredMails = MAILS.filter((mail) => includes(mail.label, condition))
                break;
            case 'Personal':
                filteredMails = MAILS.filter((mail) => includes(mail.label, condition))
                break;
            case 'Family':
                filteredMails = MAILS.filter((mail) => includes(mail.label, condition))
                break;
            default: //stared
                filteredMails = MAILS.filter((mail) => mail.starred === true)
                break;
        }
        setMails(filteredMails);
    }
    return (
        <PageContainer>
            <PageHeading title="Mail" subtile="" />
            <Box sx={{ flexGrow: 1 }}>
                <Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
                    <Box sx={{ width: 260 }}>
                        <MailSideBar mails={MAILS} handleMailStatus={handleMailStatus} />
                    </Box>
                    <Box sx={{ ml: 2, flex: 1 }}>
                        <MailContent mails={mails} />
                    </Box>
                </Box>
            </Box>
        </PageContainer>
    )
}
